import React, { useEffect, useState } from 'react';
import { AddWorkoutLogModal } from './AddWorkoutLogModal';

/**
 * A wrapper component that renders the AddWorkoutLogModal globally
 * and listens for events to open it from anywhere in the application.
 */
export const GlobalWorkoutModal: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  useEffect(() => {
    const handleOpenModal = () => {
      console.log('GlobalWorkoutModal: Opening from event');
      setIsOpen(true);
    };
    
    // Listen for the custom event to open the modal
    window.addEventListener('open-add-workout-modal', handleOpenModal);
    window.addEventListener('open-log-modal', handleOpenModal);
    
    // Call once on mount to check if we should already be open
    if (window.location.hash === '#add-workout') {
      handleOpenModal();
      window.location.hash = '';
    }
    
    return () => {
      window.removeEventListener('open-add-workout-modal', handleOpenModal);
      window.removeEventListener('open-log-modal', handleOpenModal);
    };
  }, []);

  return (
    <AddWorkoutLogModal
      isOpen={isOpen}
      onClose={() => setIsOpen(false)}
    />
  );
};

export default GlobalWorkoutModal;
