import React, { useMemo, useState } from 'react';
import { useWorkoutLogStore } from '@/store/workoutLogStore';
import { useAppStore } from '@/store/appStore';
import { Button } from '@/components/ui/button';
import { Plus, Clock, Zap } from 'lucide-react';
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import {
  Card,
  CardContent,
} from "@/components/ui/card";
import { AddWorkoutLogModal } from './AddWorkoutLogModal';
import { WorkoutLogEntry } from '@/types/Workout_Log';

export const QuickAddButton: React.FC = () => {
  const { workoutLogs } = useWorkoutLogStore();
  const { currentView } = useAppStore();
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const [selectedExercise, setSelectedExercise] = useState<Partial<WorkoutLogEntry> | undefined>(undefined);
  // Only show the button on relevant pages
  // Force to always show until we debug the currentView issue
  const shouldShow = true;
  
  // Get the most recent 4 unique exercises
  const recentExercises = useMemo(() => {
    if (!workoutLogs || workoutLogs.length === 0) return [];
    
    // Sort by date, most recent first
    const sortedLogs = [...workoutLogs].sort((a, b) => 
      new Date(b.date).getTime() - new Date(a.date).getTime()
    );
    
    // Get unique exercise+muscle group combinations
    const uniqueExercises = new Map<string, WorkoutLogEntry>();
    
    sortedLogs.forEach(log => {
      const key = `${log.exercise}-${log.muscleGroup}`;
      if (!uniqueExercises.has(key)) {
        uniqueExercises.set(key, log);
      }
    });
    
    // Return up to 4 most recent exercises
    return Array.from(uniqueExercises.values()).slice(0, 4);
  }, [workoutLogs]);
  
  const handleQuickAdd = (exercise: WorkoutLogEntry) => {
    setSelectedExercise({
      muscleGroup: exercise.muscleGroup,
      exercise: exercise.exercise,
      weight: exercise.weight,
      reps: exercise.reps,
      rating: 3, // Default rating
      restTime: exercise.restTime
    });
    setIsAddModalOpen(true);
  };
    // Only render the component if it should be shown
  // Force to always show
  console.log('QuickAddButton shouldShow:', shouldShow);
  if (!shouldShow) return null;
    return (
    <>      <div className="fixed bottom-20 right-4 sm:bottom-28 sm:right-1 md:bottom-8 md:right-28 z-40 ">
        <Popover>
          <PopoverTrigger>
            <Button
              className="h-14 w-14 rounded-full shadow-lg bg-secondary hover:bg-secondary/90 text-secondary-foreground transition-transform hover:scale-110"
              variant="default"
              title="Quick Add Recent Exercises"
            >
              <Clock className="h-6 w-6" />
            </Button>
          </PopoverTrigger>
          <PopoverContent align="end" className="w-80 p-0">
            <div className="p-4 bg-secondary/10 border-b">
              <h4 className="font-medium text-base">Quick Add Exercise</h4>
              <p className="text-sm text-muted-foreground">Quickly add a recent exercise to your log</p>
            </div>
            {recentExercises.length > 0 ? (
              <div className="max-h-[300px] overflow-auto">
                {recentExercises.map((exercise) => (
                  <Card key={`${exercise.exercise}-${exercise.id}`} className="border-0 rounded-none hover:bg-accent/5 transition-colors">
                    <CardContent className="p-4 cursor-pointer flex items-center" onClick={() => handleQuickAdd(exercise)}>
                      <div className="flex-1">
                        <h4 className="font-medium text-sm">{exercise.exercise}</h4>
                        <p className="text-xs text-muted-foreground">{exercise.muscleGroup}</p>
                        <div className="flex items-center gap-4 mt-1 text-xs text-muted-foreground">
                          <span className="flex items-center gap-1">
                            <Clock className="h-3 w-3" />
                            {exercise.reps} reps
                          </span>
                          <span className="flex items-center gap-1">
                            <Zap className="h-3 w-3" />
                            {exercise.weight} kg
                          </span>
                        </div>
                      </div>
                      <Button size="sm" variant="ghost">Add</Button>
                    </CardContent>
                  </Card>
                ))}
              </div>
            ) : (
              <div className="p-4 text-center">
                <p className="text-muted-foreground">No recent exercises found</p>
                <p className="text-xs text-muted-foreground mt-1">Add workouts to see them here for quick access</p>
              </div>
            )}
            <div className="p-2 border-t bg-secondary/5">
              <Button 
                variant="outline" 
                className="w-full"
                onClick={() => {
                  setSelectedExercise(undefined);
                  setIsAddModalOpen(true);
                }}
              >
                <Plus className="h-4 w-4 mr-2" />
                New Exercise
              </Button>
            </div>
          </PopoverContent>
        </Popover>
      </div>

      <AddWorkoutLogModal 
        isOpen={isAddModalOpen} 
        onClose={() => {
          setIsAddModalOpen(false);
          setSelectedExercise(undefined);
        }}
        initialData={selectedExercise ? {
          date: new Date().toISOString().split("T")[0],
          muscleGroup: selectedExercise.muscleGroup || '',
          exercise: selectedExercise.exercise || '',
          reps: selectedExercise.reps || 0,
          weight: selectedExercise.weight || 0,
          rating: selectedExercise.rating || 3,
          restTime: selectedExercise.restTime || null
        } : undefined}
      />
    </>
  );
};
