
import { useExercisesStore } from '@/store/exercisesStore';
import { useWorkoutLogStore } from '@/store/workoutLogStore';

/**
 * Refreshes all application data by clearing caches and re-fetching data
 * @returns {Promise<void>} A promise that resolves when all data has been refreshed
 */
export async function handleRefreshData(): Promise<void> {
  console.log('[handleRefreshData] Starting data refresh...');
  
  try {
    // Clear API cache via service worker if available
    if (navigator.serviceWorker && navigator.serviceWorker.controller) {
      navigator.serviceWorker.controller.postMessage({
        type: 'CLEAR_API_CACHE',
        cacheName: 'sheets-api-cache',
      });
      console.log('[handleRefreshData] Sent message to SW to clear sheets-api-cache.');
    }
    
    // Get store states
    const exerciseStore = useExercisesStore.getState();
    const workoutLogStore = useWorkoutLogStore.getState();
    
    // Refresh exercises data with force refresh
    console.log('[handleRefreshData] Refreshing exercises data...');
    await exerciseStore.fetchExercises(true);
    
    // Refresh workout logs data with force refresh
    console.log('[handleRefreshData] Refreshing workout logs data...');
    await workoutLogStore.fetchWorkoutLogs(true);
    
    console.log('[handleRefreshData] Data refresh completed successfully');
    return Promise.resolve();
  } catch (error) {
    console.error('[handleRefreshData] Error refreshing data:', error);
    return Promise.reject(error);
  }
}
